import React from 'react';

export default class Skills extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
    	arr:[{
    		i:<i className="fa fa-html5"></i>,
    		h:'HTML 5',
    		p:'熟悉 HTML5 网站的架构和开发'
    	},{
    		i:<i className="fa fa-css3"></i>,
    		h:'CSS3',
    		p:'熟悉使用 CSS3 写响应式网站、动画等'
    	},{
    		i:<i className="fa fa-code"></i>,
    		h:'JavaScript',
    		p:'熟悉使用 JS 制作程序、交互与特效'
    	}]
    };
  }

  render() {
  	let cnt= [];
		for(var i = 0;i < this.state.arr.length;i++){
			cnt.push(
				<div className="col-s-6 col-m-4 center-fixed" key={i}>
            <div className="skills-icon">
                {this.state.arr[i].i}
            </div>
            <div className="skills-title">
                <h3>{this.state.arr[i].h}</h3>
                <p>{this.state.arr[i].p}</p>
            </div>
        </div>
			)
		}
    return (
    	<section id="skills">
        <div className="wrap">
            <h2 className="title">掌握的技能</h2>
            <div className="row scrollable">
                {cnt}
            </div>
        </div>
    </section>
    );
  }
}


