import React from 'react';

export default class Story extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
    	
    };
  }

  render() {
    return (
    <section id="story">
        <div className="wrap">
            <h2 className="title">个人履历</h2>
            <div className="row">
                <div className="col-m-8">
                    <ul className="timeline">
                        <li><h6>2016/09-2017/10 | 郑州冠普网络科技有限公司</h6></li>
                        <li><h6>互联网|电子商务|民营|50-99人</h6></li>
                        <li><h6>web前端开发工程师|6000-8000元/月</h6></li>
                        <li><p>工作描述：<br />负责公司网页排版布局，html+css布局工作;<br />根据要求对网页进行修改、增加网页特效，使用户有更好的体验；<br />运用Ajax进行数据交互；<br />解决网页在各大浏览器中的兼容问题；</p></li>
                        <li><p>----------------------------------</p></li>
                        <li><h6>2017/11-2018/06 | LogInn</h6></li>
                        <li><h6>互联网|电子商务|民营|10-15人</h6></li>
                        <li><h6>web前端开发工程师|10000-12000元/月</h6></li>
                        <li><p>工作描述：<br />负责公司项目整个前端部分的架构；</p></li>
                        <li><p>----------------------------------</p></li>
                    </ul>
                </div>
                <div className="col-m-4 center-fixed">
                    <img src="./img/story.png"/>
                </div>
            </div>
        </div>
    </section>
    );
  }
}


