import React from 'react';

export default class DrawerUndockedExample extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
    	open: true,
    	arr:[{
    		href:'#info',
    		tit:'资料'
    	},{
    		href:'#skills',
    		tit:'技能'
    	},{
    		href:'#story',
    		tit:'履历'
    	},{
    		href:'#works',
    		tit:'作品'
    	},]
    };
  }

  render() {
  	let a= [];
		for(var i = 0;i < this.state.arr.length;i++){
			a.push(<a key={i} href={this.state.arr[i].href}>{this.state.arr[i].tit}</a>)
		}
    return (
        <aside className="sidebar">
		    <div className="avatar">
		        <img src="./img/avatar.jpg"/>
		    </div>
		    <nav className="nav">
		        {a}
		    </nav>
		</aside>
    );
  }
}


