import React from 'react';

export default class DrawerUndockedExample extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
    	arr:[{
    		img:<img src="./img/carousel.png"/>,
    		p:'轮播图'
    	},{
    		img:<img src="./img/shopping.png"/>,
    		p:'购物车'
    	},{
    		img:<img src="./img/tab.png"/>,
    		p:'选项卡'
    	}]
    };
  }

  render() {
  	let cnt= [];
		for(var i = 0;i < this.state.arr.length;i++){
			cnt.push(
				<div className="col-s-6" key={i}>
		        <div className="works-item">
		            {this.state.arr[i].img}
		            <p>{this.state.arr[i].p}</p>
		        </div>
		    </div>
			)
		}
    return (
      <section id="works">
        <div className="wrap">
            <h2 className="title">个人作品</h2>
            <div className="row">
                {cnt}
            </div>
        </div>
   	  </section>
    );
  }
}


